import React from "react";
import "./Explore.scss";
import "../../assets/img/giveawayFigure-image.png";
import Button from "../Button/Button";
import DottedBackground from "../DottedBackground/DottedBackground";
import Sensor from "../../assets/img/sensor.png";
import H from "../../assets/img/H.svg";
import Wifi from "../../assets/img/Wifi.svg";
import Waves from "../../assets/img/Waves.svg";
import Battery from "../../assets/img/Battery.svg";
import Bluetooth from "../../assets/img/Bluetooth.svg";
import Money from "../../assets/img/Money.svg";
import { Grid, GridItem } from "@material-ui/core";

export default function Explore(props) {
  const { ...rest } = props;
  return (
    <div className="explore" style={{  }}>
      <div className="background" />
      <div
        id="explore"
        style={{
          maxWidth: 960,
          width: "100%",
          margin: "auto",
          position: "relative"
        }}
        {...rest}>
        <p className="title">EXPLORE</p>

        <DottedBackground width="800" height="500" top="-108px" left="300px" />
        <DottedBackground
          styleProps={{ opacity: 0.5 }}
          width="184"
          height="80"
          top="100px"
          left="800px"
        />
        {/* <DottedBackground styleProps={{opacity:0.5}} width="204" height="114" top="532px" left="-78px"/> */}

        <div className="body">
          <div item className="left">
            <div className="sensorPresentation">
              <img alt="Sensor" src={Sensor} />
              <img alt="H" className="sensorIcon sensorTop" src={H} />
              <img alt="H" className="sensorIcon sensorTopRight" src={Money} />
              <img
                alt="H"
                className="sensorIcon sensorBottomRight"
                src={Battery}
              />
              <img
                alt="H"
                className="sensorIcon sensorBottom"
                src={Bluetooth}
              />
              <img
                alt="H"
                className="sensorIcon sensorBottomLeft"
                src={Waves}
              />
              <img alt="H" className="sensorIcon sensorTopLeft" src={Wifi} />
            </div>
          </div>
          <div className="right">
            <p>
              <span
                className="heavy heading"
                style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
                FEATURES
              </span>
              <br />
              <br />
              <span className="regular">
                Packed with 6D sensors, Bluetooth connectivity with PosturePal
                app, 12-hour battery life/300-hour standby time, and wireless
                charging.
                <br />
                <br />
                Go Sensor has your back. All in a minimalist, ergonomic design
                measuring at only 26mm ( ~1 inch). No doubt, staying upright +
                active has never been so easy!
              </span>
            </p>

            <span
              style={{
                fontSize: 740,
                position: "absolute",
                top: -305,
                zIndex: -1,
                left: 124,
                color: "white"
              }}
              className="heavy">
              E
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}
