import React from "react";
import "./Steps.scss";
import "../../assets/img/giveawayFigure-image.png";
import Button from "../Button/Button";
import DottedBackground from "../DottedBackground/DottedBackground";
import Sensor from "../../assets/img/sensor.png";
import H from "../../assets/img/H.svg";
import Wifi from "../../assets/img/Wifi.svg";
import Waves from "../../assets/img/Waves.svg";
import Battery from "../../assets/img/Battery.svg";
import Bluetooth from "../../assets/img/Bluetooth.svg";
import Money from "../../assets/img/Money.svg";
import GoodPosture from "../../assets/img/goodPosture.png";
import BadPosture from "../../assets/img/badPosture.png";
import Step1 from "../../assets/img/step1.png";
import Step2 from "../../assets/img/step2.png";
import Step3 from "../../assets/img/step3x2.png";

import { Grid, GridItem } from "@material-ui/core";

export default function Steps(props) {
  const { ...rest } = props;
  return (
    <div className="stepsBackground">
      <div className="steps" style={{ position: "relative" }}>
        <p className="title">STEPS</p>
        <DottedBackground width="680" height="360" top="120px" left="-120px" />
        <DottedBackground
          styleProps={{ opacity: 0.5 }}
          width="112"
          height="136"
          top="120px"
          left="52px"
        />
        <div className="body">
          {/* <DottedBackground styleProps={{opacity:0.5}} width="204" height="114" top="532px" left="-78px"/> */}

          {/* <div className="stepsLeft"> */}
          <span className="steps--bigletter heavy">03</span>
          <div className="images">
            <div id="step1" style={{ textAlign: "center" }}>
              <img width="240" src={Step1} />
              <p className="bold">Turn on the device</p>
            </div>
            <div id="step2" style={{ textAlign: "center" }}>
              <img width="240" src={Step2} />
              <p className="bold">Put it on your back</p>
            </div>
            <div id="step3" style={{ textAlign: "center" }}>
              <img width="300" src={Step3} />
              <p className="bold">Connect it with your phone</p>
            </div>
          </div>

          {/* </div> */}
        </div>
      </div>
    </div>
  );
}
