import React from "react";
import "./Slouching.scss";
import "../../assets/img/giveawayFigure-image.png";
import Button from "../Button/Button";
import DottedBackground from "../DottedBackground/DottedBackground";
import Sensor from "../../assets/img/sensor.png";
import H from "../../assets/img/H.svg";
import Wifi from "../../assets/img/Wifi.svg";
import Waves from "../../assets/img/Waves.svg";
import Battery from "../../assets/img/Battery.svg";
import Bluetooth from "../../assets/img/Bluetooth.svg";
import Money from "../../assets/img/Money.svg";
import SlouchingImage from "../../assets/img/slouchingImage.png";
import BadPosture from "../../assets/img/badPosture.png";
import { Grid, GridItem } from "@material-ui/core";

export default function Slouching(props) {
  const { ...rest } = props;
  return (
    <div style={{ backgroundColor: "#F4F9FC" }}>
      <div
        className="slouching"
        id="slouching"
        style={{
          maxWidth: 960,
          width: "100%",
          margin: "auto",
          position: "relative",
          paddingBottom: 128
        }}
        {...rest}>
        <p className="title">SLOUCHING</p>
        <DottedBackground width="800" height="500" top="-108px" left="144px" />
        <DottedBackground
          styleProps={{ opacity: 0.5 }}
          width="160"
          height="120"
          top="100px"
          left="758px"
        />
        <div className="body">
          {/* <DottedBackground styleProps={{opacity:0.5}} width="204" height="114" top="532px" left="-78px"/> */}

          {/* <div className="slouchingLeft"> */}
          <div>
            <span className=" bigletter heavy" />

            <p
              style={{
                width: 300,
                lineHeight: 1.8
              }}>
              <span
                className="heavy heading"
                style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
                THE NEW NORM
              </span>
              <br />
              <br />
              <span
                className="regular"
                style={{ fontSize: "1rem", width: 250, letterSpacing: 0 }}>
                Slouching has become the new norm. Countless hours are spent
                hunched over screens and devices  whether at work, home or gym
                 leading to an unattractive look and serious health issues
                <br />
                <br />
                We believe living your best life starts with proper posture.
              </span>
            </p>
          </div>
          <img
            alt="Slouching woman image"
            height="314px"
            src={SlouchingImage}
          />
        </div>
      </div>
    </div>
  );
}
