import React from 'react'
import './DottedBackground.scss'

const horizontalSpacing = 7.1+10;
const verticalSpacing = 8.5+10;
const horizontalMargin = 8;
const verticalMargin = 11.5;

function createDots(width, height, styleProps){
  
  const hDots = (width/horizontalSpacing)
  const vDots = (height/verticalSpacing)


  let dots = new Array()
  for(var i=0; i<hDots; i++){
    for(var k=0; k<vDots; k++){
      dots.push(<div className="dot" style={{...styleProps}}/>)
    }
  }
  return dots
}

export default function DottedBackground(props){
  const {width, height, styleProps, ...rest} = props

  return <div style={{overflow:"hidden", zIndex:-1, position:"absolute", width:Number(width), height:Number(height), ...rest}}>
    {createDots(Number(width), Number(height), styleProps)}
  </div>
}