import React, { useState } from "react";
import "./Posture.scss";
import "../../assets/img/giveawayFigure-image.png";
import Button from "../Button/Button";
import DottedBackground from "../DottedBackground/DottedBackground";
import Sensor from "../../assets/img/sensor.png";
import H from "../../assets/img/H.svg";
import Wifi from "../../assets/img/Wifi.svg";
import Waves from "../../assets/img/Waves.svg";
import Battery from "../../assets/img/Battery.svg";
import Bluetooth from "../../assets/img/Bluetooth.svg";
import Money from "../../assets/img/Money.svg";
import GoodPosture from "../../assets/img/goodPosture.png";
import BadPosture from "../../assets/img/badPosture.png";
import { Grid, GridItem } from "@material-ui/core";
import ChevronRight from "../ChevronRight";
import ChevronLeft from "../ChevronLeft";

export default function Posture(props) {
  const { ...rest } = props;
  const [postureTab, setPostureTab] = useState(true);

  return (
    <div
      id="posture"
      className="posture"
      style={{
        maxWidth: 960,
        width: "100%",
        margin: "auto",
        position: "relative"
      }}
      {...rest}>
      <p className="title">POSTURE</p>

      <DottedBackground width="432" height="324" top="64px" left="600px" />
      <DottedBackground width="432" height="324" top="0" left="-360px" />

      <DottedBackground
        styleProps={{ opacity: 0.5 }}
        width="64"
        height="242"
        top="104px"
        left="4px"
      />
      <div justify="space-between" alignItems="center" className="body">
        {/* <DottedBackground styleProps={{opacity:0.5}} width="204" height="114" top="532px" left="-78px"/> */}

        {/* <div className="postureLeft"> */}
        <span className=" bigletter heavy">P</span>

        <p
          style={{
            marginTop: 124,
            lineHeight: 1.8
          }}
          className="showOnDesktop">
          <span
            className="heavy heading"
            style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
            GOOD POSTURE
          </span>
          <br />
          <br />
          <span
            className="regular"
            style={{ fontSize: "1rem", letterSpacing: 0 }}>
            Pain relief
            <br />
            Better breathing
            <br />
            More energy
            <br />
            Better digestion
            <br />
            Looking more attractive
            <br />
            Increased productivity
            <br />
            Proper spinal alignment
          </span>
        </p>

        <div className="images">
          <img
            id="goodPosture"
            alt="Good Posture Image"
            height="575px"
            src={GoodPosture}
          />

          <img
            id="badPosture"
            alt="Bad Posture Image"
            height="524px"
            src={BadPosture}
          />
        </div>

        {/* </div> */}
        {/* <div className="postureRight"> */}
        <p
          style={{
            marginTop: 124,
            lineHeight: 1.8
          }}
          className="showOnDesktop">
          <span
            className="heavy heading"
            style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
            BAD POSTURE
          </span>
          <br />
          <br />
          <span
            className="regular"
            style={{ fontSize: "1rem", letterSpacing: 0 }}>
            Muscle strain/back pain
            <br />
            Lower-limb pain
            <br />
            Reduced sleep
            <br />
            Obesity
            <br />
            Decreased life expectancy
            <br />
            Gastrointestinal issues
          </span>
        </p>

        <div className="showOnMobile">
          <ChevronLeft
            className="navigation"
            onClick={() => {
              console.log(postureTab);
              setPostureTab(!postureTab);
            }}
          />
          {postureTab === true ? (
            <p
              style={{
                lineHeight: 1.8
              }}>
              <span
                className="heavy heading"
                style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
                GOOD POSTURE
              </span>
              <br />
              <br />
              <span
                className="regular"
                style={{ fontSize: "1rem", letterSpacing: 0 }}>
                Pain relief
                <br />
                Better breathing
                <br />
                More energy
                <br />
                Better digestion
                <br />
                Looking more attractive
                <br />
                Increased productivity
                <br />
                Proper spinal alignment
              </span>
            </p>
          ) : (
            <p
              style={{
                lineHeight: 1.8
              }}>
              <span
                className="heavy heading"
                style={{ letterSpacing: "0.1em", fontSize: "1.5rem" }}>
                BAD POSTURE
              </span>
              <br />
              <br />
              <span
                className="regular"
                style={{ fontSize: "1rem", letterSpacing: 0 }}>
                Muscle strain/back pain
                <br />
                Lower-limb pain
                <br />
                Reduced sleep
                <br />
                Obesity
                <br />
                Decreased life expectancy
                <br />
                Gastrointestinal issues
              </span>
            </p>
          )}
          <ChevronRight
            className="navigation"
            onClick={() => {
              console.log(postureTab);
              setPostureTab(!postureTab);
            }}
          />
        </div>

        {/* </div> */}
      </div>
    </div>
  );
}
