import React from "react";
import "./Giveaway.scss";
import GiveawayFigure from "../../assets/img/giveawayFigure";
import "../../assets/img/giveawayFigure-image.png";
import Button from "../Button/Button";
import { Grid, GridItem } from "@material-ui/core";
import DottedBackground from "../DottedBackground/DottedBackground";

export default function Giveaway(props) {
  const { ...rest } = props;
  return (
    <div className="giveaway" style={{ maxWidth: 960, width: "100%", margin: "auto", marginBottom:124 }} {...rest}>
      <div className="giveawayContainer">
        <DottedBackground width="666" height="282" top="-120px" left="154px" />
        <DottedBackground width="522" height="214" top="384px" left="-346px" />
        <DottedBackground
          styleProps={{ opacity: 0.5 }}
          width="204"
          height="114"
          top="488px"
          left="-78px"
        />
      </div>

      <div className="giveawayHeading">
        <div className="backgroundDecoration" />
        <div style={{ position: "relative" }}>GIVEAWAY</div>
      </div>
      <div className="giveawayBody">
        <div className="left">
          <p className="text">
            <span className="heavy">PosturePal</span> is the smallest bluetooth
            wearable sensor, that helps you fix poor posture.
            <br />
          </p>
          <p className="text heavy">Get the chance to win 50 free devices.</p>
          <div
            style={{
              display: "flex",
              marginTop: 48,
              justifyContent: "space-between",
              maxWidth: 376
            }}>
            <Button name="about" type="dark">
              SUBSCRIBE
            </Button>
            <Button type="light">READ MORE</Button>
          </div>
        </div>
        <div className="right">
          {/* <img width="545px" style={{position:"absolute"}} src={GiveawayFigureModel} /> */}
          <GiveawayFigure />
        </div>
      </div>
    </div>
  );
}
