import React from "react";
import Button from "../Button/Button";
import Logo from "../Logo";
import LogoText from "../LogoText";
import instagram from "../../assets/img/instagram.svg";
import twitter from "../../assets/img/twitter.svg";
import "./Footer.scss";
import facebook from "../../assets/img/facebook.svg";

export default function Footer(props) {
  return (
    <div
      id="about"
      className="footer"
      style={{
        bottom: 0,
        backgroundColor: "#4AA1CB"
      }}>
      <div className="body">
        <div className="socialandcontact">
          <div
            className="logo"
            style={{ display: "flex", alignItems: "center" }}>
            <Logo
              style={{
                color: "#FFF",
                width: "34.251",
                marginRight: 12,
                height: "43"
              }}
            />
            <LogoText
              style={{
                color: "#FFF",
                fontSize: "large",
                width: "114.645",
                height: "17.126"
              }}
            />
          </div>
          <div className="followandcontact">
            <div className="social">
              <p class="heavy" style={{ color: "#20578D" }}>
                FOLLOW US
              </p>
              <img style={{ marginRight: 16 }} src={instagram} />
              <img style={{ marginRight: 16 }} src={facebook} />

              <img src={twitter} />
            </div>
            <div className="contactus">
              <p class="heavy" style={{ color: "#20578D" }}>
                CONTRACT US
              </p>
              <a
                class="bold"
                style={{ textDecoration: "none", color: "#FFF" }}
                href="#">
                mathieuhendrikx@posturepal.io
              </a>
            </div>
          </div>
        </div>
        <div className="subscription">
          <p className="win heavy" style={{ color: "#FFF", fontSize: 32 }}>
            Want to win 50 free devices?
          </p>
          <div class="input">
            <input
              placeholder="johnsmith@gmail.com"
              style={{

              }}
              type="text"
            />
            <Button type="dark">SUBSCRIBE</Button>
          </div>
        </div>
      </div>
    </div>
  );
}
